import {User_API} from "../API/api";
import {UpdateObjectInArray} from "../utils/object_helper";
import {usersType} from "../typs/types";
import {AppStateType} from "./redux_store";
import {ThunkAction} from "redux-thunk";

const FOLOW = "FOLOW"
const UNFOLOW = "UNFOLOW"
const Users = "Users"
const RequestPages = "RequestPages"
const SettotalCount = "SettotalCount"
const TooglISFetching = "TooglISFetching"
const togol_is_folowing_progress = 'togol_is_folowing_progress'


let inicialization_state = {
    users: [
        /*
                {id:0, folowed:true, name:'sasha', status:'my status',  like_count:5,   location:{citi:'kyiv',Country:" ukraine"} ,foto_url:"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQze5-Xfl-jx40c19S0ESudQa65YYSzYsUP1w&usqp=CAU"},
                {id:1, folowed:true, name:'sasha', status:'my status',  like_count:5,   location:{citi:'kyiv',Country:" ukraine"} ,foto_url:"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQze5-Xfl-jx40c19S0ESudQa65YYSzYsUP1w&usqp=CAU"},
                {id:2, folowed:true, name:'sasha', status:'my status',  like_count:5,   location:{citi:'kyiv',Country:" ukraine"} ,foto_url:"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQze5-Xfl-jx40c19S0ESudQa65YYSzYsUP1w&usqp=CAU"},
                {id:3, folowed:true, name:'sasha', status:'my status',  like_count:5,   location:{citi:'kyiv',Country:" ukraine"} ,foto_url:"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQze5-Xfl-jx40c19S0ESudQa65YYSzYsUP1w&usqp=CAU"},
                {id:4, folowed:true, name:'sasha', status:'my status',  like_count:5,   location:{citi:'kyiv',Country:" ukraine"} ,foto_url:"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQze5-Xfl-jx40c19S0ESudQa65YYSzYsUP1w&usqp=CAU"},
                {id:5, folowed:true, name:'sasha', status:'my status',  like_count:5,   location:{citi:'kyiv',Country:" ukraine"} ,foto_url:"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQze5-Xfl-jx40c19S0ESudQa65YYSzYsUP1w&usqp=CAU"},
        */
    ] as Array<usersType>,
    PageSize: 30 ,
    TotalUsersCount: 1 ,
    requestPage: 15 ,
    IsFetching: false as boolean,
    followingInProgress: [] as Array<number>// array of users id
}
type inicialization_stateType = typeof inicialization_state

const User_reduser = (state = inicialization_state, action: actionTypes): inicialization_stateType => {
    switch (action.type) {
        case FOLOW:
            return {
                ...state,
                users: UpdateObjectInArray(state.users, action.id, 'id',
                    {followed: true})
                // users: state.users.map(u => {
                //     if (u.id === action.id)
                //         return {...u, followed: true}
                //     return u
                // })
            }
        case UNFOLOW:
            return {
                ...state,
                users: UpdateObjectInArray(state.users, action.id, 'id',
                    {followed: false})
            }
        case Users:
            return {...state, users: action.users}
        case RequestPages:
            return {...state, requestPage: action.requestPage}
        case SettotalCount:
            return {...state, TotalUsersCount: action.totalCount}
          case TooglISFetching:
             return {...state, IsFetching: action.fetching}
        case togol_is_folowing_progress:
            return {
                ...state,
                followingInProgress: action.ISfetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        default:
            return state
    }
}
type actionTypes = FolowSucsessActionType | UnFolowSucsessActionType | SetUserActionType |
    SetRequestActionType | SetTotalCountActionType | Toogol_folowing_progressActionType |SetFetchingType

/*     AC*/
type FolowSucsessActionType = {
    type: typeof FOLOW,
    id: number
}
export const FolowSucsess = (id: number): FolowSucsessActionType => ({type: FOLOW, id})
type UnFolowSucsessActionType = {
    type: typeof UNFOLOW,
    id: number
}
export const UnFolowSucsess = (id: number): UnFolowSucsessActionType => ({type: UNFOLOW, id})
type SetUserActionType = {
    type: typeof Users,
    users: Array<usersType>
}
export const SetUsers = (users: Array<usersType>): SetUserActionType => ({type: Users, users})
type SetRequestActionType = {
    type: typeof RequestPages,
    requestPage: number
}
export const SetrequestPage = (requestPage: number): SetRequestActionType => ({
    type: RequestPages,
    requestPage: requestPage
})
type SetTotalCountActionType = {
    type: typeof SettotalCount,
    totalCount: number
}
export const SetTotalCount = (totalCount: number): SetTotalCountActionType => ({
    type: SettotalCount,
    totalCount: totalCount
})
type SetFetchingType  = {
    type: typeof TooglISFetching
    fetching:boolean
}
export const SetFetching = (fetching: boolean):SetFetchingType => ({type: TooglISFetching, fetching})
type Toogol_folowing_progressActionType = {
    type: typeof togol_is_folowing_progress,
    ISfetching: boolean,
    userId: number
}
export const Toogol_folowing_progress = (ISfetching: boolean, userId: number): Toogol_folowing_progressActionType => ({
    type: togol_is_folowing_progress,
    ISfetching,
    userId
})

/*type getStatteType=()=>AppStateType
type DispatchType =  Dispatch<actionTypes>*/

type thunkType = ThunkAction<Promise<void>, AppStateType, unknown, actionTypes>
export const getUsersThunkCreator = (curentPage: number, PageSize: number): thunkType =>
    async (dispatch /*:DispatchType*/ , getStatte)  /*: getStatteType*/ => {
        dispatch(SetFetching(true))
        let data = await User_API.Get_users(curentPage, PageSize)
        dispatch(SetUsers(data.items))
        dispatch(SetTotalCount(data.totalCount))
        dispatch(SetFetching(false))

    }
export const onPageChangeThunkCreator = (curentNumber: number, PageSize: number):thunkType => async (dispatch) => {
    dispatch(SetrequestPage(curentNumber))
    dispatch(SetFetching(true))
    let data = await User_API.Get_users(curentNumber, PageSize)
    dispatch(SetUsers(data.items))
    dispatch(SetFetching(false))

}
export const _Follow_Unfolo_Flow = async (dispatch: any, id: number, apiMothod: any, actionCreator: any) => {
    /**    **/
    dispatch(Toogol_folowing_progress(true, id))
    let response = await apiMothod(id)
    if (response.data.resultCode === 0) {
        dispatch(actionCreator(id))
    }
    /**    **/
    dispatch(Toogol_folowing_progress(false, id))
}
export const follow = (id: number) => async (dispatch: any) => {
    let apiMothod = User_API.folow.bind(id)
    let actionCreator: (id: number) => FolowSucsessActionType;
    actionCreator = FolowSucsess;

    _Follow_Unfolo_Flow(dispatch, id, apiMothod, actionCreator)
}
export const unfollow = (id: number) => async (dispatch: any) => {
    _Follow_Unfolo_Flow(dispatch, id, User_API.unfolow.bind(id), UnFolowSucsess)
}

export default User_reduser


