import React from 'react';
import Switch from 'react-dom'
import './App.css';
import Nav from "./components/nav/nav"
import New from "./components/new/new";
import {BrowserRouter, HashRouter, Redirect, Route, withRouter} from "react-router-dom";
//import Dialogs from "./components/Dialogs/dialogs";
//const Dialogs    = React.lazy(() => import ("./components/Dialogs/dialogs"))

import Music from "./components/Music/Music";
import Users_conteyner from "./components/Users/users_conteyner";
import HeaderContainer from "./components/Header/HeaderConteiner";
import VideoC from "./components/video/VideoC";
import For_write_form from "./components/redux-form-for_TRAINE/for writing";
import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {Make_inicialization} from "./redux/App_reduser";
import Preloader from "./components/Preloader/Preloader/Preloader";
import store from "./redux/redux_store";
import {With_Suspense} from "./HOC/with_Suspense";
// import {With_outh_redirect} from "./HOC/With_outh_redrect";
import Love from "./components/Love/Love";
import Kozak from "./components/kozak/kozak";
import Lviv from "./components/Lviv/Lviv";
import Noga from "./components/Noga/Noga";
import Friends from "./components/Friends/Friends";
import FriendsForTrainMe from "./components/FriendsForTrainMe/FriendsForTrainMe";
import Status from "./components/status/status";
import StatusHooks from "./components/statusHooks/statusHooks";
import Coments from "./components/HoolsKoments/Coment";

const Dialogs = React.lazy(() => import(`./components/Dialogs/dialogs`));
const Profile_pageC = React.lazy(() => import(`./components/Profile/Profile_pageConteiner`))
///import Login_conteyner from "./components/Login/Login";
// const Login_conteyner = React.lazy(() => import(`src/components/Login/Login`))
// const Login_conteyner = React.lazy(() => import(`src/components/Login/Login`))

// Profile_pageC from "./components/Profile/Profile_pageConteiner";


class App extends React.Component {
    catchErorrs = (promiseRejectEvent) => {
        console.log(promiseRejectEvent)
    }

    componentDidMount() {
        this.props.Make_inicialization()
        window.addEventListener('unhandledrejection', this.catchErorrs)
    }

    componentWillUnmount() {
        window.removeEventListener('unhandledrejection', this.catchErorrs)

    }

    render() {
        if (!this.props.inicialized) {
            return <Preloader/>
        }
        return (
            <div className="App-wrapper">
                <HeaderContainer  classname="header-wraper"/>
                <Nav/>
                <div className="App-wrapper-content">
                    {/*<Switch >*/}
                    <Route path="/Profile_page/:userId?" render={With_Suspense(Profile_pageC)}/>
                    <Route exact path="/"
                           render={() => <Redirect to={'/Profile_page'}/>}/>
                    <Route path="/Dialog" render={() => {
                        return <React.Suspense fallback={<div>  </div>}>
                            <Dialogs/>
                        </React.Suspense>
                    }}/>
                    <Route path="/New" component={New}/>
                    <Route path="/Video" render={() => <VideoC/>}/>
                    <Route path="/Music" render={() => <Music/>}/>
                    <Route path="/Users" render={() => <Users_conteyner pageTitle={'Users page'} />}/>

                    {/*<Route exact path="/Login" render={() => {*/}
                    {/*    return <React.Suspense fallback={<div></div>}>*/}
                    {/*        <Login_conteyner/>*/}
                    {/*    </React.Suspense>*/}
                    {/*}}/>*/}
                    <Route path="/For_write_form" render={() => <For_write_form/>}/>
                    <Route path="/Love" render={() => <Love/>}/>
                    <Route path="/Kozak" render={() => <Kozak/>}/>
                    <Route path="/noga" render={()=><Noga />}/>
                    <Route path="/Lviv" render={() => <Lviv/>}/>
                    <Route path="/Friends" render={() => <Friends />}/>
                    <Route path="/FriendsForTrainMe" render={() => <FriendsForTrainMe />}/>
                    <Route path="/Status" render={() => <Status />}/>
                    <Route path="/StatusHooks" render={() => <StatusHooks />}/>
                    <Route path="/Coments" render={() => <Coments />}/>
                    {/*<Route path="*" render={() => <div> 404 not foutd</div>}/>*/}

                    {/*///</Switch >*/}
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    inicialized: state.App.inicialized
})


let App_container = compose(
    withRouter,
    connect(mapStateToProps, {Make_inicialization}))
(App)

const Samurai_App = () => {
    return (
        //basename={process.env.PUBLIC_URL}
        <HashRouter >
            <Provider store={store}>
                <App_container/>
            </Provider>
        </HashRouter>
    )
}
export default Samurai_App
