import React from "react";
import pag from './paginator.module.css'
const PaginatorForFriends = (props)=>{

    let pages = []
    let pageNumber  = Math.ceil(props.props.totalCount / props.props.pageSize)
    for (let a   = 1 ; a <= pageNumber;a++ ){
        pages.push(a)
    }
    return <div className={pag.all} >

        {pages.map(i=>{
            return <a  onClick={(e)=>props.props.ChangeCurentPage(props.props.pageSize,i)}>{i}</a>
        })}
    </div>
}

export default PaginatorForFriends

