import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import profile_reducer from "./Profile_reducer";
import dialog_reducer from "./dialog_reducer";
import video_reducer from "./video_reducer";
import navbar_reducer from "./navbar_reducer";
import Users_reducer from "./Users_reducer";
import Auth_reducer from "./Auth_reduser";
import thunkMiddleware from 'redux-thunk'
import {reducer as formReducer} from 'redux-form'
import App_reducer from "./App_reduser";
import loveReducer from "./Love";
import Kozachky_reducer from "./kozachky_reducer";
import Lviv_reducer from "./Lviv_reducer";
import NogaReducer from "./Noga reducer";
import FriendsReducer from "./FriendsReducer";
import FriendsForTrainMeReducer from "./FriendsForTrainMe_reducer";
import Comon_reducer from "./Common reducer";
import State_reducer from "./Status_reducer";
import Status_reducer from "./Status_reducer";


let reducers = combineReducers(
        {
        profile_page: profile_reducer,
        dialog_page: dialog_reducer,
        video_page: video_reducer,
        navbar_page: navbar_reducer,
        users_page: Users_reducer,
        auth: Auth_reducer,
        App: App_reducer,
        form: formReducer,
        lovePage:loveReducer,
        kozachy:Kozachky_reducer,
        Lviv:Lviv_reducer,
        Noga:NogaReducer,
        FriendsPage:FriendsReducer,
        FriendsForMe:FriendsForTrainMeReducer,
            Comon:Comon_reducer,
        Status_Page:Status_reducer
    }

)

type reducersType = typeof reducers
export  type AppStateType = ReturnType<reducersType>

     // @ts-ignore
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
 const store = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddleware)));

//let store = createStore(reducers, applyMiddleware(thunkMiddleware));

// @ts-ignore
window.store = store
export default store











