import {createSelector} from "reselect";
import {AppStateType} from "./redux_store";


export const userss_for_reselect = (state:AppStateType) => {
    return state.users_page.users.filter(u => true);
}


export const users =  createSelector(userss_for_reselect,(users)=> {
    return users.filter(u => true);
})


export const PageSize = (state:AppStateType) => {
    return state.users_page.PageSize
}
export const TotalUsersCount = (state:AppStateType) => {
    return state.users_page.TotalUsersCount
}
export const requestPage = (state:AppStateType) => {
    return state.users_page.requestPage
}
export const IsFetching = (state:AppStateType) => {
    return state.users_page.IsFetching
}
export const followingInProgress = (state:AppStateType) => {
    return state.users_page.followingInProgress
}


