import React  from "react";
import {connect} from "react-redux";
import {newApi, newText} from "../../redux/kozachky_reducer";
import * as axios from "axios";
class KozakInner extends React.Component {

    componentDidMount () {
        axios.get(`https://reqres.in/api/users?page=2`).then(then=>{
            this.props.setApi(then.data.data[0].email)
        })
        // let fromButton=(text)=>{
        //     this.props.setApi(text)
        // }
        // return <div>
        //     <form action="">
        //         <input type="text"  />
        //         <button onClick={fromButton}>from api22222</button>
        //         <div><h1>{this.props.apiWord}</h1></div>
        //     </form>
        // </div>
    }
    render() {
        let leter = React.createRef()
        let addLetter = () => {
            this.props.addLetter(leter.current.value)
        }
        return <div>
            <form action="">
                <input type="text" onChange={addLetter} ref={leter}/>
                <button >from api22222</button>
                <div><h1>{this.props.changeLater}</h1></div>
                <div><h1>{this.props.apiWord}</h1></div>
            </form>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        changeLater: state.kozachy.changeLater,
        apiWord: state.kozachy.apiWord
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        addLetter: (text) => {
            let action = newText(text)
            dispatch(action)
        },
        setApi:(text)=>{
            dispatch(newApi(text))
        }
    }
}

const Kozak = connect(mapStateToProps, mapDispatchToProps)(KozakInner)
export default Kozak



