import React from "react";
import clas from "./e.module.css"

import {
    Toogol_folowing_progress,
    getUsersThunkCreator,
    onPageChangeThunkCreator,
    follow,
    unfollow
} from "../../redux/Users_reducer"

import {connect} from "react-redux";
import UserFuncComponent from "./usersFunc";
import Preloader from "../Preloader/Preloader/Preloader";
import {compose} from "redux";
import {With_outh_redirect} from "../../HOC/With_outh_redrect";
import {requestPage, IsFetching, PageSize, TotalUsersCount,} from "../../redux/users_selectors";
import {usersType} from "../../typs/types";
import {AppStateType} from "../../redux/redux_store";
type MapStatePropsType={
    curentPage:number
    pageSize:number
    IsFetching:boolean
    TotalUsersCount:number
    users:Array<usersType>
    followingInProgress:Array<number>

}
type MapdispatchPropsType={
    getUsersThunkCreator:(curentPage:number,pageSize:number)=> void
    onPageChangeThunkCreator:(curentPage:number,pageSize:number)=>void
    follow:(id:number)=>void
    unfollow:(id:number)=>void
    Toogol_folowing_progress:(    ISfetching: boolean, userId:number)=>void
}
type ownPropsType={
    pageTitle:string
}
type PropsType= MapStatePropsType & MapdispatchPropsType & ownPropsType
class UserC extends React.Component< PropsType > {

    componentDidMount = () => {
        const {curentPage,pageSize} =  this.props
        this.props.getUsersThunkCreator(curentPage, pageSize)
    }
    OnPageChange=(curentNumber:number)=>{
        const {pageSize}= this.props
        this.props.onPageChangeThunkCreator(curentNumber,pageSize)
    }
    render() {

        return <>
            <div className={clas.PageName}><h2 >{this.props.pageTitle}</h2></div>

            {this.props.IsFetching ? <div className={clas.preloader}><Preloader/> </div> : null}
            <div className={clas.allUsers}>
                <UserFuncComponent
                    OnPageChange={this.OnPageChange}
                    TotalUsersCount={this.props.TotalUsersCount}
                    PageSize={this.props.pageSize}
                    users={this.props.users}
                    requestPage={this.props.curentPage}
                    follow={this.props.follow}
                    unfollow={this.props.unfollow}
                    followingInProgress={this.props.followingInProgress}

                />
            </div>

        </>
    }
}

let MapStateToProps = (state:AppStateType):MapStatePropsType => {
    return {
        users: state.users_page.users,
        pageSize:  PageSize(state),
        TotalUsersCount: TotalUsersCount(state),
        curentPage: requestPage(state),
        IsFetching: IsFetching(state),
        followingInProgress: state.users_page.followingInProgress,

    }}

export default compose(
    With_outh_redirect,
    //TStateProps = {}, TDispatchProps = {}, TOwnProps = {}, State = DefaultState
connect<MapStatePropsType,MapdispatchPropsType,ownPropsType, AppStateType>(
    MapStateToProps,{follow, unfollow, Toogol_folowing_progress,
    getUsersThunkCreator, onPageChangeThunkCreator})
)(UserC)



