import React, {FC} from "react";
import silka from "../../acets/images/Foto/standatr_foto_ForAva.png";
import userscss from "./e.module.css"
import {NavLink} from "react-router-dom";
import {usersType} from "../../typs/types";

type propsType ={
    requestPage:number
    unfollow:(id:number)=>void
    follow:(id:number)=>void
    OnPageChange:()=> void
    user:usersType
    props:any
}

let UserF:FC<propsType> = ({requestPage, unfollow, follow,
                    OnPageChange,user, ...props}) => {
    return<div>
                <span>
                    <div>
                        <NavLink to={'/Profile_page/' + user.id}>
                        <img className={userscss.ava}
                             src={user.photos.small != null ?
                                 user.photos.small :
                                 silka} alt=""/>
                        </NavLink>
                    </div>
                    <div>
                    {user.followed ?
                        // @ts-ignore
                        <button disabled={props.props.followingInProgress.some(id => id === user.id)}
                                onClick={() => {unfollow(user.id)}}> UnFolow</button>
                        // @ts-ignore
                        : <button disabled={props.props.followingInProgress.some(id => id === user.id)}
                                onClick={() => {follow(user.id)}}> Folow</button>}

                    </div>
                </span>
        <span>
                    <span>
                    <div>{user.name}</div><div>{user.status}</div>
                                                    </span>
                                <    span>
      <div>{"user.location.citi"}</div>
     <div>{"user.location.Country"}</div>
      </span>
                                </span>

    </div>


}
export default UserF


