import React from "react";
import {connect} from "react-redux";
import {OnChangeAC} from "../../redux/Lviv_reducer";
import {CountComponent} from "../../HOC/With_outh_redrect";
import foto from './../../acets/images/norwey.jpg'
import clas from './Lviv.module.css'
import Item from "./One_foto";

const LvivIner = (props) => {
    let refInput = React.createRef()
    let changeMasage = () => {
        props.addText(refInput.current.value)
    }

    return <div>
        lviv
        <input type="text" ref={refInput} onChange={changeMasage}/>
        <div>{props.wordLviv}</div>

        <div className={clas.conteiner}>
            <ul>
                <Item/>
                <Item/>
                <Item/>
                <Item/>
                <Item/>
                <Item/>

            </ul>

        </div>

    </div>
}

const LvivExt = CountComponent(LvivIner)


let mapStateToProps = (state) => {
    return {
        wordLviv: state.Lviv.wordLviv
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        addText: (text) => {
            let action = OnChangeAC(text)
            dispatch(action)
        }
    }
}
const Lviv = connect(mapStateToProps, mapDispatchToProps)(LvivExt)

export default Lviv

