import * as axios from "axios";


export const getStatusApi = (id) => {
    return (axios.get(`https://social-network.samuraijs.com/api/1.0/profile/status/${id}`,{
        withCredentials: true,
            headers: {'API-KEY': '85526c1a-a93d-4948-93cd-7ca1458eb874'}
    })
        .then(response => {
            return response
        }))
}


export const PutStatusApi = (status) => {
    return (axios.put(`https://social-network.samuraijs.com/api/1.0/profile/status`, {status}, {
        withCredentials: true,
        headers: {'API-KEY': '85526c1a-a93d-4948-93cd-7ca1458eb874'}
    })
        .then(response => {
            return response
        }))
}

