import React, {useState} from "react";
import clas from "./Pafinator.module.css";

type propsType = {
    totalItemsCount:number,
    PageSize:number,
    requestPage:number,
    OnPageChange:(pageNumber:number)=>void,
    portionSize:number
}


const Paginator: React.FC<propsType> = ({totalItemsCount,PageSize,  requestPage,OnPageChange,portionSize=10}) => {
    //  
    let pagesCount = Math.ceil(totalItemsCount / PageSize)
    const pages: Array<number> = [];

    for (let i = 1; i <= pagesCount; i++) {pages.push(i)}

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;

    return <div >
        { portionNumber > 1 &&
        <button onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button> }

        {pages
            .filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)
            .map((p) => {
                return <span className={ p===requestPage? clas.activePageNamber : clas.PageNamber}
                             key={p}
                             onClick={(e) => {
                                 OnPageChange(p);
                             }}>{p} </span>
            })}
        { portionCount > portionNumber &&
        <button onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button> }
    </div>
}

export default Paginator







