import React from "react";
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";
import {plusCount} from "../redux/Common reducer";
import {setState} from "../redux/FriendsForTrainMe_reducer";

let MapStateToPropsRedirect = (state) => {
    return {
        isOuth: state.auth.isOuth
    }
}
export const With_outh_redirect = (Component) => {
    class RedirectComponent extends React.Component {
        render() {
            if (!this.props.isOuth) {
                return <Redirect to={'/Login'}/>
            }
            return <Component {...this.props}/>
        }
    }

    let ConectAuth_redirect_componenta = connect(MapStateToPropsRedirect)(RedirectComponent)

    return ConectAuth_redirect_componenta

}

export const CountComponent = (Component) => {
    class NewComp extends React.Component {
        componentDidUpdate(prevProps, prevState, snapshot) {
            if (prevProps.ComonWord !== this.props.ComonWord){
                this.props.pluscount()
            }
        }
        render() {
            return <div >

                < Component onMouseOver={()=>{this.props.pluscount()}} {...this.props} />
                <p onChange={()=>{this.props.pluscount()}}> {this.props.Comoncount}</p>
            </div>
        }
    }
    const MapStateToPropsGetCount = (state) => {

        return {
            Comoncount: state.Comon.Commoncount,
            ComonWord: state.Comon.ComonWord
        }
    }
    const MapDispatchcountPlus = (dispatch) => {
        return {
            pluscount: () => {
                dispatch(plusCount())
            }
        }
    }
    let NewComp2 = connect(MapStateToPropsGetCount, MapDispatchcountPlus)(NewComp)

    return NewComp2

}




















