import React, {useEffect, useState} from "react";
import {connect} from "react-redux";
import {get_status_T, Put_status_T} from "../../redux/Profile_reducer";

const StatusHookint = (props) => {
    let [editMode, changeEditMode] = useState(false)
    let [status, setStatus] = useState(props.status)
    useEffect(()=>{
        setStatus(props.status)
    },[props.status])

    props.get_status_T(11683)
    let deactivateEditMode = () => {
        props.Put_status_T(status)
        props.get_status_T(11683)
        changeEditMode(false)

    }
    let activateEditMode = () => {
        changeEditMode(true)
    }
    const changeStatus = (e) => {

        setStatus(e.currentTarget.value)
    }
    return <div>
        {editMode === true
            ? <input type="text"
                     onDoubleClick={deactivateEditMode}
                     onChange={changeStatus}
                     value={status}
             />
            : <h3 onDoubleClick={activateEditMode}>{status} </h3>
        }
    </div>
}
let mapState = (state) => {
    return {
        status: state.profile_page.status,
        userId: state.profile_page.userId
    }
}


const StatusHooks = connect(mapState, {get_status_T,Put_status_T    })(StatusHookint)
export default StatusHooks





