import React from "react";

let ToChange = 'ToChange'
let apiWord ='apiWord'
let inicializatetState = {
    changeLater: '',
    apiWord:''
}


const Kozachky_reducer = (state = inicializatetState, action) => {
    switch (action.type) {
        case ToChange:
            return {
                ...state,
                changeLater: action.text
            }
            case apiWord:
            return {
                ...state,
                apiWord: action.text
            }
        default:
            return state
    }

}
export const newText= (text) => ({type: ToChange, text: text})
export const newApi = (text)=>({type:apiWord,text})
export default Kozachky_reducer
