import React from "react";
import classes from "./nav.module.css";
import {NavLink} from "react-router-dom";

const Nav = () => {
    return (
        <nav className={classes.nav}>
            <div className={classes.mainItems}>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to="/Profile_page">Profile page</NavLink>
                </div>
                <div className={classes.items}
                ><NavLink to="/Dialog" activeClassName={classes.activeLink}>Massages</NavLink>
                </div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/Users'> users</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/Login'> Login</NavLink></div>

            </div>


            <div className={classes.itemsForTrainy}>
                <div className={classes.uselesname}><span> my studing/training  here</span></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/Noga'> "Noga"</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/Friends'> Friends</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/FriendsForTrainMe'> FriendsForTrainMe</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink} to='/lviv'> lviv</NavLink>
                </div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/Coments'> Coments</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/StatusHooks'> StatusHooks</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/Status'> Status</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to="/Video"> video</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink} to="/Love">new</NavLink>
                </div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to="/Music"> music</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/Love'>Settings</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/For_write_form'> For_write_form</NavLink></div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink} to='/Love'> Love</NavLink>
                </div>
                <div className={classes.items}><NavLink activeClassName={classes.activeLink}
                                                        to='/kozak'> kozak</NavLink></div>
            </div>

        </nav>);
};
export default Nav