import {FollowAPI, GetState, unfollowAPI} from "../API/api";

const SET_STATE = "SET_STATE"
const FOLLOWSUCCES = 'FOLLOWSUCCES'
const UNFOLLOWSUCCES = 'UNFOLLOWSUCCES'
const inicializatedState = {

    items: [
        {
            id: 0,
            followed: true,
            name: 'sasha',
            status: 'my status',
            like_count: 5,
            location: {citi: 'kyiv', Country: " ukraine"},
            photos: {
                small: null,
                large: null,
            }
        }
    ],
    PageSize: 30,
    requestPage: 15,
    error: null,
    totalCount: 9,
    currentPage: 1
}

const FriendsForTrainMeReducer = (state = inicializatedState, action) => {
    switch (action.type) {
        case SET_STATE:
            return {
                ...state,
                items: [...action.items],
                totalCount: action.totalCount,
                error: action.error
            }
        case FOLLOWSUCCES:
            return {...state,
            items: state.items.map(item=>{
                if(item.id ===action.id){
                    return {...item, followed : true}
                }
                else{return {...item}}
            })
            }
            case UNFOLLOWSUCCES:
            return {...state,
            items: state.items.map(item=>{
                if(item.id ===action.id){
                    return {...item, followed : false}
                }
                else{return {...item}}
            })
            }


        default: {
            return state
        }
    }


}
export const setStateAC = (items, totalCount, error) => {
    return (
        {
            type: SET_STATE,
            items: items,
            totalCount: totalCount,
            error: error,
        }
    )
}

export const followSuccess = (id) => ({type: FOLLOWSUCCES, id})
export const unfollowSuccess = (id) => ({type: UNFOLLOWSUCCES, id})
export const setState = (PageSize, currentPage) => {
    return (dispatch) => {
        GetState(PageSize, currentPage).then(response => {
            dispatch(setStateAC(response.data.items,
                response.data.totalCount,
                response.data.error))
        })
    }
}
export const changePage = (PageSize, currentPage) => {
    return (dispatch) => {
        dispatch(setState(PageSize, currentPage))
        GetState(PageSize, currentPage).then(response => {
            dispatch(setStateAC(response.data.items,
                response.data.totalCount,
                response.data.error))
        })
    }
}


export const Follow = (id) => {
    return (dispatch) => {
        FollowAPI(id).then(response => {
            if (response.data.resultCode === 0) {
                debugger
                dispatch(followSuccess(id))
            }

        })
    }
}
export const unFollow = (id) => {
    return (dispatch) => {
        unfollowAPI(id).then(response => {
            if (response.data.resultCode === 0) {
                debugger
                dispatch(unfollowSuccess(id))
            }

        })
    }
}


export default FriendsForTrainMeReducer

