import React from "react";
import Paginator from "../comon/paginator/paginator"
import User1F from "./useRFunc";
import clas from './e.module.css'

const UserFuncComponent = ({
                   TotalUsersCount, requestPage, PageSize,
                   OnPageChange, users, ...props
               }) => {
    return <div>
        <div className={clas.paginator}>
            <Paginator   totalItemsCount={TotalUsersCount} requestPage={requestPage}
                         PageSize={PageSize} OnPageChange={OnPageChange}/>
        </div>

        <div className={clas.oneUser}>
            {
                users.map(u => <User1F user={u}
                                     props={props}
                                    key={u.id}
                                    unfollow={props.unfollow}
                                    follow={props.follow}
                                       folowing_progress={props.folowing_progress}/>
            )}

        </div>

    </div>
}
export default UserFuncComponent


