import React from "react";
import video_css from "./Video.module.css"
import cool_ass from "../../acets/images/Foto/3.jpg"
const Video=(props)=>{

    let video_list=props.video_on_page.video.
    map((m)=><div className={video_css.item}><a>id={m.id} name={m.name} </a>
        <div><img src={cool_ass}/></div>
    </div>)

    let add_video=React.createRef()
    let new_video=()=>{
        alert(add_video.current.value)
    };

    return(
        <div>
            {video_list}
            {/*<a> {props.state.video_on_page.video}</a>*/}
            <a> </a>
            <div>
                <h3> new Video</h3>
                <textarea ref={add_video}/><button onClick={new_video}> </button>
            </div>
        </div>
);
};


export default Video