import React from "react";
import clas from './FriendsForTrainMe.module.css'
import standartAvaHere from "./../../acets/images/Foto/standatr_foto_ForAva.png"
import {NavLink} from "react-router-dom";

const FriendFortrainMeFunc = (props) => {

    return <div>
        <div>{props.props.state.items.map(item =>
            <div className={clas.item}>
                <NavLink to={'/Profile_page/' + item.id}>
                    <div className={clas.avaDiv}><img src={item.photos.large || standartAvaHere} alt=""/></div>
                </NavLink>
                <div>name: {item.name}</div>
                <div>id: {item.id}</div>
                <div>status: {item.status}</div>
                <div>uniqueUrlName: {item.uniqueUrlName}</div>
                {
                    item.followed
                        ? < div>
                            <button onClick={() => {
                                props.props.unfollow(item.id)
                            }}>unfollow
                            </button>
                        </div>
                        : <div>
                            <button onClick={() => {
                                props.props.follow(item.id)
                            }}>follow
                            </button>
                        </div>}


            </div>)}</div>


    </div>
}
export default FriendFortrainMeFunc

