import React, {ChangeEvent} from "react";
import axios from "axios";
import {connect} from "react-redux";
import {addNameAC} from "../../redux/Love"
import {AppStateType} from "../../redux/redux_store";

type stateType = {
    newLine:string
}

type MapStatePropsType ={
    newLine:string

}
type MapdispatchPropsType ={
    addName:(text:string)=>void
}
type ownPropsType ={

}
type PropsType= MapStatePropsType & MapdispatchPropsType & ownPropsType
class InnerLove extends React.Component<PropsType,stateType> {
    componentDidUpdate(prevProps:PropsType, prevState:stateType) {
        // if (prevProps.newLine !== this.props.newLine){
        //     this.setState({
        //         newLine: this.props.newLine
        //     });
        // }
    }
    //on_status_change = (e:ChangeEvent<HTMLInputElement>) => {
    //         this.setState({
    //             status: e.currentTarget.value
    //         })
    //     }
     addLine = (e:ChangeEvent<HTMLInputElement>) => {
        this.props.addName(e.currentTarget.value)
}

    render() {
        return <div>
            <input type="text" placeholder={'input'}
                       onChange={this.addLine} value={this.props.newLine}/>
                <button>div</button>
            <div> {this.props.newLine} </div>
        </div>
    }
}
const mapStateToProps=(state:AppStateType)=>{
    return {
        newLine:state.lovePage.newLine
    }
}
const mapDispatchToProps =(dispatch:any)=>{
    return{
        addName:(text:string)=>{
            let action = addNameAC(text)
            dispatch(action)
        }

    }
}

const Love = connect<MapStatePropsType,MapdispatchPropsType,ownPropsType, AppStateType>(mapStateToProps,mapDispatchToProps)(InnerLove)

export default Love