import React from "react";
import classes from "./Video.module.css"
import {connect} from "react-redux";
import {SetVideoProfile} from "../../redux/video_reducer";
import Video from "./Video";


let MapStateToProps = (state)=> {

        return {
            video_on_page:state.video_page.video_on_page

        }

}

export default connect(MapStateToProps, {SetVideoProfile})(Video)

