import {CaptchaResultCodeEnum, CaptchaURL, OUTH_API, resultCodeEnum} from "../API/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = "SET_USER_DATA"
const SET_CAPCHA = 'SET_CAPCHA'

type inicialization_stateType1 ={
    userId: number| null,
    email: string | null,
    login: string |null,
    isOuth: boolean
    captcha: string |null,
}
let inicialization_state = {
    userId: null as number| null,
    email: null as string | null,
    login: null as string | null,
    isOuth: false as boolean | false,
    captcha: null as string |null,
}
type inicialization_stateType =typeof inicialization_state

const Auth_reducer = (state = inicialization_state, action:any):inicialization_stateType => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        case SET_CAPCHA:
            return {...state, captcha: action.captcha}

        default:
            return state
      }
}
type Set_user_outh_dataPayloadType ={
    userId:number |null
    email:string  |null
    login:string  |null
    isOuth:boolean|null
}
type setUserOuthDataTypeAction = {
 type:typeof SET_USER_DATA,
    payload:Set_user_outh_dataPayloadType
}
export const SetUserOuthData = (userId:number |null, email:string |null, login:string | null, isOuth:boolean |null,):setUserOuthDataTypeAction =>
    ({type: SET_USER_DATA, payload: {userId, email, login, isOuth}})

type setCaptchaURLActonType ={
    type: typeof SET_CAPCHA,
    payload:{captcha:string }
}

const setCaptchaURL = (captcha:string):setCaptchaURLActonType => ({type: SET_CAPCHA, payload:{captcha}})

export const outh_me_T = () => async (dispatch:any) => {

    let myData = await OUTH_API.me()

    if (myData.resultCode === resultCodeEnum.success) {
        let {id, login, email} = myData.data
        dispatch(SetUserOuthData(id, email, login, true,))
    }

}


export const Login_T = (email:string, password:string, rememberMe:boolean,captcha: null | string =null) => async (dispatch:any) => {

    let response = await OUTH_API.login(email, password, rememberMe,captcha)

    if (response.data.resultCode === 0) {
        dispatch(outh_me_T())
    }
     else {
         if (response.data.resultCode === CaptchaResultCodeEnum.CaptchaIsRequired) {
             dispatch(stopSubmit('login', {_error: response.data.massages}))

             dispatch(getCapchaURL_T())


        } else {
             let message = response. data.massages.length > 0 ? response.data.massages[0] : 'something gone wrong'

             dispatch(stopSubmit('login', {_error: message}))
         }
     }
}


export const getCapchaURL_T = () => async (dispatch:any) => {
    let response = await CaptchaURL.getCahpchaURL()
    let captcha = response.data.url

    dispatch(setCaptchaURL(captcha))
}

export const Logout_T = () =>  async (dispatch:any) => {
   let response = await OUTH_API.logOut()
            if (response.data.resultCode === 0) {
                dispatch(SetUserOuthData(null, null, null, false))
            }

}

export default Auth_reducer








