import React, {useState} from "react";
import foto1 from "../../acets/images/Foto/1.jpg";


const GaleryItem = () => {
    let [editmode, changeEditmode] = useState(false)
    let [coment, changeComent] = useState('coment')

    let activateEditmode =()=>{
        changeEditmode(true)
    }
    let deactivEditMode =()=>{
        changeEditmode(false)
    }
    let Change =(e)=>{
        changeComent(e.currentTarget.value)
    }
debugger
    return <div>
        <li>
            <a >
                <img src={foto1} alt=""/>
                {editmode === true
                    ? <input type="text"
                             placeholder={'for cometn'}
                             onBlur={deactivEditMode}
                             onChange={Change}/>
                    : <a onDoubleClick={activateEditmode}>{coment}</a>}

            </a>
        </li>
    </div>
}
export default GaleryItem



