import {outh_me_T} from "./Auth_reduser";

const Inicialized_succses = "Inicialized_succses"

export type inicialization_state_Type ={
    inicialized:boolean
}

let inicialization_state:inicialization_state_Type = {
    inicialized: false,
}

const App_reducer = (state = inicialization_state, action:any):inicialization_state_Type => {
    switch (action.type) {
        case Inicialized_succses:
            return {
                ...state,
                inicialized:true
            }
        default:
            return state
    }
}
type inicialization_succesActonType ={
    type:typeof Inicialized_succses
}


export const inicialization_succes = ():inicialization_succesActonType => ({type: Inicialized_succses})

export const Make_inicialization = () => (dispatch:any) =>{
   let promise = dispatch(outh_me_T())

    Promise.all([promise]).then( () =>{dispatch(inicialization_succes())
        })
    }

export default App_reducer












