import React from "react";
import clas from './Noga.module.css'
import foto1 from './../../acets/images/Foto/8.jpg'
import foto2 from './../../acets/images/Foto/7.jpg'
import foto3 from './../../acets/images/Foto/6.jpg'

class SliderOnJs extends React.Component {
    state = {
        Foto: foto1

    }
    componentDidMount() {
        const changeSlide = () => {
            if (this.props.slide === 1) {
                this.setState({Foto: foto1})
            } else if (this.props.slide === 2) {
                this.setState({Foto: foto2})
            } else if (this.props.slide === 3) {
                this.setState({Foto: foto3})
            }
        }
        changeSlide()
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.slide !== this.props.slide) {
            if(this.props.slide=== 1){
                this.setState({
                    Foto: foto1
                })
            }
            else if(this.props.slide=== 2){
                this.setState({
                    Foto: foto2
                })
            }else if(this.props.slide=== 3){
                this.setState({
                    Foto: foto3
                })
            }
        }
    }
    render() {
        return <div className={clas.conteiner}>
                <div className={clas.slide}>
                    <div className={clas.numberText}> {this.props.slide}</div>
                    <img src={this.state.Foto} alt={'foto'}/>
                    {/*<div className={clas.text}><span>{this.props.captionText}</span></div>*/}

                </div>
            <a className={clas.prev} onClick={()=>{this.props.PlusSlide(-1)}}>-</a>
            <a className={clas.next} onClick={()=>{this.props.PlusSlide(+1)}}>+</a>

                <div className={clas.dot}>
                    <div onClick={()=>{this.props.PlusSlide(-1)}} >000 </div>
                    <div >11 </div>
                    <div onClick={()=>{this.props.PlusSlide(+1)}} > 22</div>
                </div >
        </div>
    }
}

export default SliderOnJs



