import React from "react";
import {connect} from "react-redux";
import F from "./Friends.module.css"
import fotoStandartForHere from "./../../acets/images/Foto/standart_ava.png"
import Preloader from "../Preloader/Preloader/Preloader";
import * as axios from "axios";
import {ChangeCurentPage, follow, getUsers,} from "../../redux/FriendsReducer";
import PaginatorForFriends from "./paginatorFrirend";

class Friendiner extends React.Component {

    // setSTR=()=>{
    //     this.props.FolowingNow(true)
    //     this.props.onLoading(true)
    //     setUsers().then(data=>{this.props.setUsers(data.items)})
    //     this.props.onLoading(true)
    //     this.props.FolowingNow(false)
    //
    // }


    componentDidMount() {
        //    this.props.onLoading(true)
        //     setUsers().then(data=>{this.props.setUsers(data.items)})
        //     this.props.onLoading(true)
        this.props.getUsers(this.props.pageSize,this.props.curentPage2)
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.curentPage2 !== this.props.curentPage2){
            this.setState({
                curentPage2: this.props.curentPage2

            } ,

        )
        }
    }

    render() {
        let refId = React.createRef()
        let change = () => {
            const id = refId.current.value
        }
        const follows = () => {
            const id = refId.current.value
            this.props.follow(id)
        }

        return <>
            <PaginatorForFriends props={this.props}/>
            {this.props.IsLoading ? <div><Preloader/> sdfsdf</div> : null}
            <button> loadd</button>
            <div>
                <input type="text" ref={refId} onChange={change} placeholder={'idFollow'}/>
                <button onClick={follows}>folow</button>

            </div>
            {this.props.Items.map(t =>
                <div className={F.items}>
                    <div>
                        <button disabled={this.props.FOlowingProgres}>button for check disable</button>
                    </div>

                    {this.props.Items.followed
                        ? <span>alreddy Foollowed</span>
                        : <span>alreddy Not Foollowed</span>
                    }

                    <div><h3>name: {t.name} </h3></div>
                    <div><h4>ID: {t.id} </h4></div>
                    <div>{t.photos.large === true ? <img src={t.photos.large} alt=""/>
                        : <img src={fotoStandartForHere} alt=""/>}
                    </div>
                    <div>{t.status}</div>
                </div>
            )}

        </>
    }

}

const mapStateToProps = (state) => {

    return {
        Items: state.FriendsPage.Items,
        IsLoading: state.FriendsPage.loading,
        FOlowingProgres: state.FriendsPage.FOlowingProgres,
        pageSize: state.FriendsPage.pageSize,
        totalCount: state.FriendsPage.totalCount,
        curentPage2: state.FriendsPage.curentPage2,

    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        // setUsers: (res) => {
        //     dispatch(setUsersAC(res))
        // },
        // onLoading: (IsLoading) => {
        //     dispatch(onLoading(IsLoading))
        // },
        // FolowingNow:(FOlowingProgres)=>{
        //     dispatch(FolowingDisable(FOlowingProgres))
        // },
        // setUser:()=>{
        //     dispatch(setUser())
        // }
        getUsers: (pageSize, curentPage2) => {
            dispatch(getUsers(pageSize, curentPage2))
        },
        follow: (id) => {
            dispatch(follow(id))
        },
        ChangeCurentPage:(pageSize,curentPage2)=>{
            dispatch(ChangeCurentPage(pageSize,curentPage2))
        }


    }
}


const Friend = connect(mapStateToProps, mapDispatchToProps)(Friendiner)
export default Friend


