import React from "react";
let initialState = {
    newLine: ''
};
type initialStateType = typeof initialState
const SetGetedName = 'SetGetedName'
const loveReducer = (state = initialState, action:any):initialStateType => {
    switch (action.type) {
        case SetGetedName:
            return {
                ...state,
                newLine : action.newLine

            };
        default:
            return state;
    }
}

type addNameACType ={
    type:string
    newLine:string
}

export const addNameAC = (text:string):addNameACType => ({type: SetGetedName, newLine: text})
// export const updateNewMessageBodyCreator = (body) =>
//     ({ type: UPDATE_NEW_MESSAGE_BODY, body: body })

export default loveReducer;