import React from "react";
import {connect} from "react-redux";
import {changeStatus, getStatus} from "../../redux/Status_reducer";


class Status extends React.Component {
     state ={
    editMode:false,
    status:this.props.status
    }
    componentDidMount() {

        this.props.getStatus(11683 )
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
         if(prevProps.status !==this.props.status){
             this.setState({
                 status:this.props.status
             })
         }
    }
    activateEtitMode=()=>{
         this.setState({
             editMode:true
         })
    }
    deactivateEtitMode=()=>{
        this.setState({
            editMode:false
        })
    }
    deactivateEtitModePlus=()=>{
        this.setState({
            editMode:false
        })
            this.props.changeStatus(this.state.status)
    }
    changeLocalStatus=(e)=>{
         this.setState({
             status:e.currentTarget.value
         })
    }
    changeGeneralState=()=>{
         this.props.changeStatus(this.state.status)
     }

    render() {
        return <div>
            {this.state.editMode
            ?<div>
                    <input type="text" onChange={this.changeLocalStatus}
                    value={this.state.status}
                           onDoubleClick={this.deactivateEtitModePlus}
                           onBlur={this.deactivateEtitMode}

                    /><br/><br/>
                    <button onClick={this.changeGeneralState}> send</button>
            </div>
            :<div>
                    <span  onClick={this.activateEtitMode}> {this.props.status}</span>
            </div>}

        </div>
    }
}


let MapStateToProps = (state) => {
    return {
        status: state.Status_Page.status,
        id: state.Status_Page.id
    }
}
const statusContainer = connect(MapStateToProps, {getStatus, changeStatus})(Status)

export default statusContainer


