import React from "react";
import classes from "./Header.module.css";
import {NavLink} from "react-router-dom";
import logo from './../../acets/images/fanyLogo.png'



const HeaderContainer = (props)=> {
    /*            <div className={classes.Loginblok}>
                    {props.isOuth ? props.login : < NavLink to={'/login'}> login < /NavLink>}
                </div>
                */
debugger
    return <header className={classes.header}>
        <img src={logo} alt=""/>
        <div className={classes.Loginblok}>
            {props.isOuth ?
                <div>
                    <span>login/name: {props.login}</span><br/>
                    <span> id:{props.id}   </span> <button onClick={props.Logout_T}> logout</button></div>
                // props.id, props.logout
                :<NavLink to={'/login'}>login</NavLink> }
            </div>
    </header>
}
export default HeaderContainer