import React from "react";

let OnChange = 'OnChange'
const inicialization_State = {
    wordLviv: ''
}
const Lviv_reducer = (state=inicialization_State, action) => {
    switch (action.type) {
        case OnChange:
            return {
                ...state,
                wordLviv: action.text
            }
        default:
            return state
    }
}
export const OnChangeAC=(text)=>({type:OnChange, text:text})
export default Lviv_reducer


