import React from "react";
import {connect} from "react-redux";
import {forChange, PlusSlide} from "../../redux/Noga reducer";
import clas from './Noga.module.css'
import Galery from "./Galery";
import Animation from "./Animation";
import SliderOnJs from "./Slider";


const NogaIner = (props) => {
    return <div>
        <div className={clas.al}>
            <Animation {...props}/>
                <Galery />
            <SliderOnJs {...props}/>
        </div>


    </div>
}
const NogaExt = NogaIner

let f1 = (state) => {
    return {
        count: state.Noga.word,
        slide:state.Noga.slide,
        captionText:state.Noga.captionText
    }
}


const Noga = connect(f1, {forChange,PlusSlide})(NogaExt)
export default Noga
