import React from "react";



const Pagin = (props) => {

    let pages = []
    let numberPages =Math.ceil(props.props.state.totalCount/props.props.state.PageSize)
    for (let i =1;i!==numberPages;i++){
        pages.push(i)
    }
    let changeCurrentPage=(PageSize, page)=>{
        props.props.changePage(PageSize,page)
    }
    return <div>
        {pages.map(page=>{return <span onClick={(e)=>{changeCurrentPage(props.props.state.PageSize,page)}} > {page} </span> })}


    </div>

}
export default Pagin



