import axios, {AxiosResponse} from "axios";


const instanse = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {'API-KEY': '85526c1a-a93d-4948-93cd-7ca1458eb874'},
})
export const User_API = {
    Get_users: (requestPage = 15, PageSize = 10) => {
        return instanse.get(`users?page=${requestPage}&count=${PageSize}`).then(response => {
            return response.data
        })
    },
    unfolow(id: number) {
        return instanse.delete(`follow/` + id,
        )
    },
    folow(id: number) {
        return instanse.post(`follow/` + id, {})
    },
    get_profile(id: number) {
        console.warn('absolute method, please use profile_api ')
        return profile_api.get_profile(id)


    }
}
export const profile_api = {
    get_profile(id: number) {
        return instanse.get(`profile/` + id)
    },
    get_status(id: number) {
        return instanse.get('profile/status/' + id)
    },
    put_status(status: number) {
        return instanse.put('profile/status', {status: status})
    },
    UploudFoto(file: any) {
        const formData = new FormData()
        formData.append('image', file)
        return instanse.put('/profile/photo', formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    },
    uploadProfile(Data: any) {

        return instanse.put('profile', Data)
    }
}

export enum resultCodeEnum {
    success = 0,
    error = 1,
}

export enum CaptchaResultCodeEnum {
CaptchaIsRequired=10
}


type MeResponseType = {
    data: { id: number, email: string, login: string }
    resultCode: number
    massages: Array<string>

}
type LoginResponseType = {
    data: {
        userId:number
    }
    resultCode: number
    massages: Array<string>

}
type logOutResonseType ={
    resultCode:number
    messages: Array<string>,
        data:Array<any>

}

export const OUTH_API = {

    me() {
        return instanse.get<MeResponseType>(`auth/me`).then(res => res.data)
    },

    login(email: string, password: string, rememberMe: boolean = false, captcha: null | string = null) {
        return instanse.post<LoginResponseType>('auth/login', {email, password, rememberMe, captcha})
    },
    logOut() {
        return instanse.delete<logOutResonseType>('/auth/login')
    }
}
