export const CaptchaURL = {
    getCahpchaURL() {
        return instanse.get(`security/get-captcha-url`
        )
    },
}
export const setUsers = (count: number, page: number) => {
    return (
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?count=${count}&page=${page}`)
            .then(response => {
                return response.data
            })
    )
}
export const followUSERID = (id: number) => {
    return (
        axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${id}`, {},
            {
                withCredentials: true,
                headers: {'API-KEY': '85526c1a-a93d-4948-93cd-7ca1458eb874'}
            }
        ).then(response => {
            return response
        })
    )
}
export const GetState = (PageSize: number, currentPage: number) => {
    return (axios.get(`https://social-network.samuraijs.com/api/1.0/users?count=${PageSize}&page=${currentPage}`).then(response => {
            return response
        }
    ))
}
export const FollowAPI = (id: number) => {
    return (axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${id}`, {}, {
        withCredentials: true,
        headers: {'API-KEY': '85526c1a-a93d-4948-93cd-7ca1458eb874'}
    })).then(response => {
        return response
    })
}
export const unfollowAPI = (id: number) => {
    return (axios.delete(`https://social-network.samuraijs.com/api/1.0/follow/${id}`, {
        withCredentials: true,
        headers: {'API-KEY': '85526c1a-a93d-4948-93cd-7ca1458eb874'}
    })).then(response => {
        return response
    })
}


