
const add_masage = 'add_masage'
const changed_mesege_text = 'changed_mesege_text'
// const new_mesege_form = 'new_mesege_form'

type dialog_dataType ={
    id:string,
    name:string
}
type masage_dataType ={
    messege_item:string,
}

let inicialization_state = {
    dialog_data: [
        {name: "sasha", id: "sasha"},
        {name: "luda", id: "luda"},
        {name: "masha", id: "masha"},
        {name: "dasha", id: "dasha"},
        {name: "ardaria", id: "ardaria"},
        {name: "anarchiya", id: "anarchiya"},
    ] as  Array <dialog_dataType>,
    masage_data: [
        {messege_item: "helo"},
        {messege_item: "kak dela"},
        {messege_item: "33434"},
        {messege_item: "hel4545o"},
        {messege_item: "ddddd"},
    ] as Array<masage_dataType>,
    // changed_masage: "" as string
}
export type inicialization_stateType = typeof inicialization_state

const dialog_reducer = (state = inicialization_state, action:any):inicialization_stateType => {
    switch (action.type) {
        // case changed_mesege_text:
        //     return {
        //         ...state,
        //         changed_masage: action.text
        //     }
        case add_masage: {
            //let new_masagee = {messege_item: state.changed_masage};
            //let new_state={...state,
            return {
                ...state,
                masage_data: [...state.masage_data, {messege_item: action.new_mesege_form}],
                // changed_masage:''
            }
            //new_state.masage_data=[...state.masage_data]
            //new_state.masage_data.push(new_masagee)
            //state.changed_masage = ""
            //return new_state
        }

        default:
            return state
    }
}
type addmesege_actionType={
    type:'add_masage'
    new_mesege_form:string
}

export const addmesege_action_creator = (new_mesege_form:string):addmesege_actionType => {
    return (
        {
            type: 'add_masage',
            new_mesege_form: new_mesege_form
        })
}

type change_masege_actionType ={
    type: typeof changed_mesege_text,
    text:string

}
export const change_masege_action_creator = (changed_mesege_text:string):change_masege_actionType => {
    return (
        {
            type: 'changed_mesege_text',
            text: changed_mesege_text
        })
}

export default dialog_reducer
