import React from "react";
import Header from "./Header";
import {connect} from "react-redux";
import {Logout_T, outh_me_T} from "../../redux/Auth_reduser";


class HeaderContainer extends React.Component {
    componentDidMount() {
    this.props.outh_me_T()
    }
    render() {
        return (
            <Header {...this.props}/>
        );
    }
}
let MapStateToProps = (state) => ({
    isOuth:state.auth.isOuth,
    login:state.auth.login,
    id:state.auth.userId
})
export default connect(MapStateToProps, {outh_me_T,Logout_T})(HeaderContainer)