import {followUSERID, setUsers} from "../API/api";

const items = 'items'
const data = 'data'
const CHANGEPAGE ='CHANGEPAGE'
const Loading = 'Loading'
const FolowingInprogres = 'FolowingInprogres'
const MakeFollow='MakeFollow'
let inicializated_state = {
    IsLoading: false,
    totalCount: 10,
    error: null,
    pageSize: 6,
    curentPage2:3,
    FOlowingProgres: false,
    Items: [
        {
            name: "nrm_mrn",
            id: 13388,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: false
        },
        {
            name: "nrm_mrn",
            id: 13388,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: false
        },
    ],

}

const FriendsReducer = (state = inicializated_state, action) => {
    switch (action.type) {


        case items:
            return {
                ...state,
                Items: action.Items
            }
            case data:
            return {
                ...state,
                totalCount:action.totalCount
            }
        case Loading:
            return {
                ...state,
                IsLoading: action.IsLoading
            }
        case FolowingInprogres:
            return {
                ...state,
                FOlowingProgres: action.FOlowingProgres
            }
        case MakeFollow:
            return {
            ...state,
                Items:[...state.Items],
                followed:{...state.Items.followed}}
        case CHANGEPAGE:
            return {
                ...state,
                curentPage2: action.i
            }

        default:
            return state

    }
}
export const setUsersAC = (res) => ({type: items, Items: res})
export const setOtherData = (totalCount)=>({type:data,totalCount})
export const FolowingDisable = (FOlowingProgres) => ({type: FolowingInprogres, FOlowingProgres: FOlowingProgres})
export const onLoading = (IsLoading) => ({type: Loading, IsLoading})
export const makeFollow =(followed) =>({type:MakeFollow,followed})
export const ChangeCurentPage2 = (i)=> ({type:CHANGEPAGE, i})


export const getUsers = (pageSize,curentPage2) => {
    return (dispatch) => {
        dispatch(onLoading(true))
        dispatch(FolowingDisable(true))
        setUsers(pageSize,curentPage2 ).then(data=>{
            dispatch(setUsersAC(data.items))
            dispatch(setOtherData(data.totalCount))
        }
        )
        dispatch(onLoading(false))
        dispatch(FolowingDisable(false))

    }
}
export const ChangeCurentPage =(pageSize,curentPage2)=>{
    return (dispatch)=>{
        dispatch(ChangeCurentPage2(curentPage2))
        setUsers(pageSize,curentPage2 ).then(data=>{
                dispatch(setUsersAC(data.items))
                dispatch(setOtherData(data.totalCount))
            }
        )
    }
}

export const follow = (id)=>{
    return (dispatch) =>{
        dispatch(onLoading(true))
        dispatch(FolowingDisable(true))
            followUSERID(id).then(response=>{
dispatch(makeFollow())
            })
        dispatch(onLoading(false))
        dispatch(FolowingDisable(false))
    }
}


