import {getStatusApi, PutStatusApi} from "../API/APi_train";

let setStatus='setStatus'
let initState={
  status:'here is a status from reducer',
    id:11683
}
type  initStateType = typeof initState
const Status =(state=initState,action:any): initStateType=>{
        switch (action.type){
            case setStatus:
                return {...state,
                    status:action.text
                }
    default:
    return state
        }
}
type setStatusAC ={
    type:typeof setStatus,
    text:string
}
export const setStatusAC=(text:string): setStatusAC=>({type:setStatus,text:text})

export  const getStatus=(id:number)=> async (dispatch:any)=>{

        let response = await getStatusApi(id)
            if(response.status===200) {
                dispatch(setStatusAC(response.data))
            }
    }

export  const changeStatus=(text:string)=> async (dispatch:any)=>{

        let response = await PutStatusApi(text)
            if(response.status===200){
                dispatch(setStatusAC(text))
            }
    }



export default Status