export type inicialization_stateType = typeof inicialization_state
const profile_reducer = (state = inicialization_state, action:actionType):inicialization_stateType => {
    switch (action.type) {
        case add_post:
            return {
                ...state,
                post_data: [...state.post_data,
                    {
                        id: 41,
                        name: 'logo',
                        masage_text: action.add_post_form_text,
                        like_count: 0,
                        src: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQFGt9fVEw3Qw20dyF3' +
                            'UZF_ZU3JSAGCAisd4A&usqp=CAU',
                        post: "postsdffsdfsdf"
                    }
                ],
            }
        case DELETE_POST:
            return {
                ...state,
                post_data: state.post_data.filter(p => p.id !== action.id)

            }
        case SET_USER_PROFILE:
            return {...state, profile: action.profile}
        case SET_STATUS:
            return {...state, status: action.status}

        case SET_AVA:
            return (
                //{...state, profile: {...state.profile, photos: action.photos, } as profileType}
                {...state, profile: {...state.profile, photos:{} } as profileType}

                // {...state, profile: {...state.profile, } as profileType ,
                //     photos: {...state.profile.photos} as photosType}
                // profile: { ...state.profile,photos:{...state.profile.photos, large:action.file} } as profileType,
            )


        default:
            return state
    }
}
type actionType = addPost_actionType | delete_post_Type| SetUserProfileAction| setAvaAtionType
| set_statusActonType

type addPost_actionType ={
    type:typeof add_post,
    add_post_form_text:string
}
export const addPost_action_creator = (add_post_form_text:string):addPost_actionType => {
    return (
        {
            type: add_post,
            add_post_form_text: add_post_form_text
        }
    )
}
type delete_post_Type ={
    type:typeof DELETE_POST,
    id:number
}
export const delete_post_AC = (id:number):delete_post_Type => {
    return (
        {
            type: DELETE_POST,
            id: id
        }
    )
}
type SetUserProfileAction = {
    type: typeof SET_USER_PROFILE
    profile: profileType
}
export const SetUserProfile = (profile:profileType):SetUserProfileAction => ({type: SET_USER_PROFILE, profile: profile})
type setAvaAtionType ={
    type: typeof SET_AVA
    file?: photosType | null
    foto_error?:any
}
export const setAva = (file:photosType):setAvaAtionType => ({type: SET_AVA, file})
type set_statusActonType ={
    type: typeof SET_STATUS
    status: string
}

type thunkType= ThunkAction<void, AppStateType, unknown, actionType>
export const set_status = (status:string):set_statusActonType => ({type: SET_STATUS, status: status})
export const get_user_T =  (userId:number) =>  async (dispatch:any) => {
    let response =await User_API.get_profile(userId)
            dispatch(SetUserProfile(response.data))

    }
export const get_status_T = (userId:any) => async (dispatch:any) => {
    let response = await profile_api.get_status(userId)
    dispatch(set_status(response.data))
}
export const uploadProfile = (Data:any) => async (dispatch:any, getState:any) => {

    let id = getState().auth.userId
    let response = await profile_api.uploadProfile(Data)
    let message = response.data.messages.length > 0 ? response.data.messages[0]
        : 'something gone wrong'
    if (response.data.resultCode === 0) {
        dispatch(get_user_T(id))
    }

}
export const Put_status_T = (status:any) => async (dispatch:any) => {
    try {
        let response = await profile_api.put_status(status)
        if (response.data.resultCode === 0) {
            dispatch(set_status(status))
        }
    } catch (error) {

    }

}


export const onUpload = (file:any) => async (dispatch: any) => {
    let response = await profile_api.UploudFoto(file)
    if (response.data.resultCode === 0) {
        dispatch(setAva(response.data.data.photos))
    }
}


export default profile_reducer







