import React from "react";
import FriendFortrainMeFunc from "./FriendFortrainMeFunc";
import {connect} from "react-redux";
import {changePage, Follow, setState, unFollow} from "../../redux/FriendsForTrainMe_reducer";
import Pagin from "./pagin";


class FriendFortrainMe extends React.Component {
    componentDidMount() {
        this.props.getSetState(this.props.PageSize,this.props.currentPage)


    }

    render() {

        // if(this.props.isOuth.isOuth ===false) return <Redirect to={'Login'}/>
        return <div>
            <Pagin props={this.props}/>
            <FriendFortrainMeFunc props={this.props}/>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        state: state.FriendsForMe,
        isOuth:state.auth
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        getSetState:(PageSize,currentPage)=>{
            dispatch(setState(PageSize,currentPage))
        },
        changePage:(PageSize,currentPage)=>{
            dispatch(changePage(PageSize,currentPage))
        },
        follow:(id)=>{
        dispatch (Follow(id))
        },
        unfollow:(id)=>{
        dispatch (unFollow(id))
        }
}
}

const FriendFortrainMe2 = connect(mapStateToProps, mapDispatchToProps)(FriendFortrainMe)
export default FriendFortrainMe2



