import React from "react";
const Add_video='add_video'
const SET_VIDEO_PROFILE= 'SET_VIDEO_PROFILE'

let inicialization_state={
    video_on_page:{
        video:[
            {id:1,name:111}
        ]
    }
}


const video_reducer=(state=inicialization_state, action)=>{
    switch (action.type) {
        case SET_VIDEO_PROFILE:
            return {...state, video_on_page: action.video}

        default:
            return  state
    }
}

export const SetVideoProfile=(video)=>({type:SET_VIDEO_PROFILE,video: video})
export default video_reducer









