import React, {useEffect, useState} from "react";
import image1 from './../../acets/images/Foto/1.jpg'
import clas from './Coment.module.css'

const Coment = () => {
    let [editmode, changeEditmode] = useState(false)
    let [coment, changeComente] = useState('coment')
    let activateMode =()=>{
        changeEditmode(true)
    }
    let deactivateMode =()=>{
        changeEditmode(false)
    }
    let changeComment =(e)=>{
        debugger
        changeComente(e.currentTarget.value)
    }
    return <div>
        <div className={clas.item}>
            <div className={clas.image}>
                <img src={image1} alt={""}/>
            </div>
            <div>
                {editmode === true
                    ? <input type="text" onDoubleClick={deactivateMode}
                             placeholder={'coment'}
                             value={coment}
                    onChange={changeComment}/>
                    : <p onDoubleClick={activateMode}>{coment}</p>}
            </div>

        </div>
    </div>
}

export default Coment